<template>
	<view class="evaluate">
		<view class="evaluate-news">
			<view class="news-box flexd">
				<view class="img-box">
					<image src="/static/HM-chat/img_ps.png" mode=""></image>
				</view>
				<view class="name-box flexd">
					<view class="name">
						<text class="font36">-</text>
					</view>
					<view class="star flexd flex-center">
						<text class="font22" style="margin-right: 10rpx;">店铺评价</text>
						<uni-rate v-model="rateValue" @change="onChange" />
					</view>
				</view>
			</view>
			<view class="content-box">
				<text class="font22 fontColor-ccc">{{content.length}}/100</text>
				<textarea style="padding:10rpx;background-color: #F8F8F8;" v-model="content" :maxlength="100"
					placeholder-style="color:#CCCCCC" placeholder="说说店铺的优点吧，您的鼓励特别重要" />
			</view>
		</view>
		<!-- 提交退款 -->
		<view class="sub">
			<view class="sub-btn" @click="evaluateEvent">
				<span>提交</span>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userId: '',
				// 订单ID
				orderId: 0,
				// 评价内容
				content: '',
				// 打分
				rateValue: 0,
				// 订单信息
				form: {
					"address": { //地址信息
						"address": "", //地址信息
						"address_detail": "", //详细地址
						"id": 0, //编号
						"is_default": 0, //是否为默认地址 0否 1是
						"latitude": "", //纬度
						"longitude": "", //经度
						"name": "", //收件人
						"phone": "", //手机号
						"userId": 0 //用户ID
					},
					"order": { //订单信息
						"createtime": "2022-7-28 11:55", //创建时间
						"delivery_time": "", //配送时间
						"finish_time": "", //完成时间
						"order_id": 0, //订单id
						"order_num": "13131", //订单编号
						"order_price": "500", //订单金额
						"order_status": 2, //订单状态(1待付款 2待配送 3配送中 4已完成 5取消订单)
						"pay_time": "", //支付时间
						"pay_type": 0, //支付类型 0支付到个人 1微信 2支付宝(普通)
						"payment_type": 0, //支付来源(1.手机网页,2.PC,3.原生APP支付 4小程序)(普通)
						"pro_price": 100, //商品总额
						"remarks": "aaaaaaaaaaa", //备注
						"serial_number": "", //交易流水号
						"totalCount": 0 //商品总数
					},
					"orderDetails": [ //订单详情
						{
							"buy_count": 20, //购买数量
							"buy_price": 50, //购买单价
							"order_details_id": 0, //订单详情id
							"order_id": 0, //订单id
							"pro_id": 0, //商品id
							"pro_image": "", //商品图片
							"pro_name": "哒哒哒" //商品名称
						}
					]
				},
			}
		},
		onLoad(e) {
			this.userId = uni.getStorageSync('userId')
			this.orderId = e.id
			// this.loadmore()
		},
		methods: {
			// 获取订单详情
			loadmore() {
				let that = this
				this.util.ajax('order/myOrdersDetails', {
					orderId: this.orderId
				}, res => {
					that.form = res.data
				})
			},
			// 评价订单
			evaluateEvent() {
				let that = this
				if (that.rateValue == 0 || that.content == '') {
					that.$alert('请先评价再提交')
				} else {
					that.util.ajax('order/evaluateOrder', {
						"content": that.content,
						"orderId": that.orderId,
						"score": that.rateValue,
						"userId": that.userId
					}, res => {
					that.$alert('评价成功')
					setTimeout(() => {
						that.$jump('/pages/user/orderDetail?id=' + that.orderId)
					}, 1000)
					})
				}
			},
			// 监听评分
			onChange(e) {
				console.log('评分发生改变:' + JSON.stringify(e))
				// console.log(this.rateValue);
			}
		}
	}
</script>

<style lang="less">
	.evaluate {
		padding: 30rpx;

		.evaluate-news {
			padding: 41rpx;
			border-radius: 20rpx;
			background-color: #fff;
			margin-bottom: 50rpx;

			.news-box {
				margin-bottom: 40rpx;

				.img-box {
					width: 130rpx;
					height: 130rpx;

					image {
						width: 100%;
						height: 100%;
					}
				}

				.name-box {
					flex-direction: column;
					justify-content: space-around;
					padding-left: 20rpx;

					.name {
						text {
							font-weight: bold;
						}
					}
				}
			}

			.content-box {
				position: relative;

				.font22 {
					position: absolute;
					bottom: 0;
					right: 0;
					z-index: 1;
				}
			}
		}
	}

	.sub {
		line-height: 91rpx;
		text-align: center;

		.sub-btn {
			margin: 18rpx 100rpx;
			background: #FAD134;
			border-radius: 100rpx;
		}
	}
</style>
